@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #f5ecdd;
  color: #8d7c58;
}

.content {
  background-image: url("./img/bg-image_7.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  padding: 80px 0 80px 80px;
  position: relative;
}

.content::before {
  content: "";
  background: linear-gradient(to right, #b8aa8d 25%, rgba(202, 175, 117, 0.2));
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
